.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.15s ease-in-out, visibility 0.15s ease-in-out;
    overflow: hidden;
}

.overlayVisible {
    visibility: visible;
    opacity: 1;
    .drawer {
        transform: translateX(0);
    }
} 

.drawer {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 420px;
    height: 100%;
    right: 0;
    background: #fff;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    padding-right: 0;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;

    .items {
      flex: 1;
      overflow: auto;
      padding-right: 20px;
    }

    .totalBlock {
      padding-top: 20px;
      padding-right: 40px;

      ul {
        margin-bottom: 30px;

        li {
          display: flex;
          align-items: flex-end;
          margin-bottom: 20px;
  
          div {
            flex: 1;
            height: 1px;
            border-bottom: 1px dashed #eaeaea;
            position: relative;
            top: -4px;
            margin: 0 6px;
          }
        }
      }

      .greenButton {
        position: relative;

        &:hover {
          img {
            transform: translateX(10px);
          }
        }
        img {
          position: absolute;
          right: 30px;
          top: 21px;
          transition: transform 0.6s ease-in-out;
        }
      }


    }

    .cartHeader {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      padding-right: 40px;

      img {
        opacity: 0.6;
        transition: opacity 0.2 ease-in-out;

        &:hover {
          opacity: 1;
          cursor: pointer;
        }
      }
    }

    h2 {
      margin: 0;
    }

    .cartItem {
      display: flex;
      align-items: center;
      border: 1px solid #eaeaea;
      border-radius: 8px;
      overflow: hidden;
      padding: 10px 20px;
      margin-bottom: 20px;
      margin-right: 10px;

      .cartItemImg {
        height: 70px;
        width: 70px;
        background-size: contain;
        background-position: 0px 0px;
        background-repeat: no-repeat;
        margin-right: 20px;
      }

      .picture {
        margin-right: 20px;
      }

      .cartInfo {
        flex: 1;
      }

      p {
        margin: 0;
        font-size: 16px;
      }

      .removeBtn {
        opacity: 0.6;
        transition: opacity 0.2 ease-in-out;

        &:hover {
          opacity: 1;
          cursor: pointer;
        }
      }
    }
  }