.card {
    border: 1px solid #eaeaea;
    border-radius: 20px;
    width: 200px;
    margin-right: 30px;
    margin-bottom: 30px;
    padding: 20px;
    transition: box-shadow 0.2s ease-in-out, transform 0.3s ease-in-out;

    .plus {
      cursor: pointer;
    }

    &:hover {
      box-shadow: 0px 18px 32px rgba(0, 0, 0, 0.06);
      transform: translateY(-5px);
    }

    .favorite {
      position: absolute;
      cursor: pointer;
    }

    span {
      opacity: 0.7;
      margin-right: 4px;
    }
    
    p {
      text-align: center;
      font-weight: 600;
      font-size: 14px;
    }
  
    .cardBottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
      padding-bottom: 10px;

      .cardInfo {
        display: flex;
      }
    
