* {
  box-sizing: border-box;
  outline: none;
  font-family: system-ui;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f4e9;
}

.greenButton {
  width: 100%;
  height: 55px;
  background: #9dd558;
  border: none;
  border-radius: 8px;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: box-shadow 0.3s ease-in-out, background 0.2s ease-in-out, transform 0.3s ease-in-out;

  &:disabled {
    background: #bebebe;
    box-shadow: 0px 6px 10px rgba(51, 59, 43, 0.9);
    cursor: default;
    animation: button-loading 0.3s ease-in-out;
  }

  &:hover {
    background: darken($color: #9dd558, $amount: 5%);
    box-shadow: 0px 6px 10px rgba(71, 138, 13, 0.9);
    transform: translateY(-4px);
  }

  &:active {
    background: darken($color: #9dd558, $amount: 10%);
  }
}

.cartEmpty {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1;
  text-align: center;

  img {
    margin-bottom: 10px;
  }

  p {
    width: 240px;
    margin-top: 0;
    margin-bottom: 10px;
  }

  .greenButton {
    width: 280px;
    position: relative;
    padding: 0;
    margin-top: 10px;

    &:hover {
      img {
        transform: rotate(180deg) translateX(10px);
      }
    }

    img {
      position: absolute;
      top: 20px;
      right: 230px;
      transform: rotate(180deg);
      transition: transform 0.3s ease-in-out;
    }
  }
}

.wrapper {
  background: #fefefe;
  box-shadow: 0px 10px 20px rgba($color: #f8e8b9, $alpha: 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;

  .content {
    padding: 40px;
    padding-top: 0px;

    .searchStr {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;

      p {
        font-size: 26px;
      }
    }
    .searchBlock {
      display: flex;
      align-items: center;
      border: 1px solid #eaeaea;
      border-radius: 8px;
      padding: 0 15px;
      position: relative;

      .clear {
        position: absolute;
        top: 2px;
        right: 2px;
        width: 18px;
        height: 18px;
        cursor: pointer;
      }

    }

    input {
      border: 0;
      padding: 10px;
      font-size: 16px;
      opacity: .5;
      width: 200px;
    }

    .sneakers {
      display: flex;
      flex-wrap: wrap;
    }

    .textBlock {
      margin-bottom: 40px;
      font-size: 20px;
      font-weight: 600;
    }
  }
}


header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px;
  border-bottom: 1px solid #eaeaea;

  .headerLeft {
    display: flex;
    align-items: center;

    img {
      margin-right: 15px;
    }

    h3 {
      text-transform: uppercase;
    }

    h3, 
    p {
      margin: 0;
    }
  }

  .headerRight {
    display: flex;

    li {
      margin-right: 14px;
      cursor: pointer;
    }
    li:last-child {
      margin-right: 0;
      cursor: pointer;
    }
    span {
      margin-left: 10px;
      font-size: 18px;
      font-weight: 600;
    }
  }
}

.logo {
  width: 40px;
  height: 40px;
  margin: 20px auto;
  background: #595959;
  border-radius: 20px;
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}